import React, { Component } from 'react'
import { Button, Typography } from '@material-ui/core';
import { withRouter } from 'react-router'
import { Link } from 'react-router-dom'

class Intro extends Component {
    constructor(props) {
        super(props);
        localStorage.clear();
    }
    
    render() {
        return (
            <div>
                <Typography variant='body1' paragraph>
                    ¡Bienvenida a la búsqueda del tesoro!
                </Typography>
                <Typography variant='body1' paragraph>
                    A partir de las pistas, vas a tener que ir escribiendo las respuestas para poder avanzar y encontrar el tesoro.
                </Typography>
                <Typography variant='body1' paragraph>
                    Tené en cuenta una cosa: la búsqueda del tesoro es una búsqueda posta,
                    así que vas a tener que salir a caminar para encontrar las respuestas.
                </Typography>
                <Typography variant='body1' paragraph>
                    Suerte y espero que encuentres el tesoro. :)</Typography>
                <Typography variant='body2' paragraph>
                    Podés pedirme pistas por WhatsApp ;)
                </Typography>

                <Link to='/questions'>
                    <Button color='primary' variant='contained'>
                        Comenzar la búsqueda
                    </Button>
                </Link>
            </div>
        );
    }
}

export default withRouter(Intro);
